import CryptoJS from "crypto-js";

function generateKey(num: number) {
  const key = Math.random()
    .toString(36)
    .repeat(num)
    .slice(2, num + 2);
  localStorage.setItem("key", key);
  return key;
}

export default {
  key: localStorage.getItem("key") || generateKey(16),

  encrypt(word: string) {
    const keyStr = this.key;
    const key = CryptoJS.enc.Utf8.parse(keyStr);
    const srcs = CryptoJS.enc.Utf8.parse(word);
    const encrypted = CryptoJS.AES.encrypt(srcs, key, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7,
    });
    return encrypted.toString();
  },

  decrypt(word: string, key?: string) {
    try {
      const keyStr = key || this.key;
      const k = CryptoJS.enc.Utf8.parse(keyStr);
      const decrypted = CryptoJS.AES.decrypt(word, k, {
        mode: CryptoJS.mode.ECB,
        padding: CryptoJS.pad.Pkcs7,
      });
      return CryptoJS.enc.Utf8.stringify(decrypted).toString();
    } catch (e) {
      this.key = generateKey(16);
    }
  },
};
