import genealogy from "@/utils/genealogy";
import aes from "./aes";

export interface GenealogyInfo {
  children?: Person[];
  showChildren?: boolean;
  level?: number;
  current?: boolean;
  show?: boolean;
}

export interface PersonInfo {
  name: string;
  pname?: string;
  id: number;
  pid: number;
  gender: number;
  mate?: Person | Person[];
  keywords?: string[];
  lunarBirthDay?: string;
  solarBirthday?: string;
  age?: number;
  origin?: string;
  image?: string;
  tags?: string[];
  phone?: string;
  die?: boolean;
}

export interface Person extends PersonInfo, GenealogyInfo {}

const genealogyInfoList: Person[] = genealogy.map((item) => {
  const person: Person = {
    ...item,
  };
  person.showChildren = false;
  person.current = false;
  person.show = true;
  person.die && (person.name += person.gender ? "公" : "婆");
  if (person.mate) {
    if (Array.isArray(person.mate)) {
      person.mate.forEach((item) => {
        item.die && (item.name += item.gender ? "公" : "婆");
      });
    } else {
      person.mate.die && (person.mate.name += person.mate.gender ? "公" : "婆");
    }
  }
  return person;
});

export const genealogyList = aes.encrypt(JSON.stringify(genealogyInfoList));

export function listToTree(list: Person[], pid = -1, level = 30): Person[] {
  const father = list.find((item) => item.id === pid);
  return list
    .filter((item) => item.pid === pid)
    .map(
      (item) => (
        (!father || father?.gender) && level > 0 && (item.level = level),
        (item.children = listToTree(
          list,
          item.id,
          item.gender && level > 0 ? level + 1 : -1
        )),
        item
      )
    );
}

export function debounce(fn: Function, delay: number) {
  let timer: number | null = null;
  return (...args: any[]) => {
    if (timer) {
      clearTimeout(timer);
    }
    timer = setTimeout(() => {
      fn(...args);
      timer = null;
    }, delay);
  };
}

export function addFont(url: string) {
  const myFont = new FontFace("PingFang SC", `url(${url})`);
  myFont.load().then(function (loaded) {
    document.fonts.add(loaded);
  });
}
