import aes from "./aes";

export function isAuthenticated() {
  const token = localStorage.getItem("token");
  if (token) {
    const word = aes.decrypt(token);
    if (word) {
      const [name, date] = word.split("|");
      if (date) {
        const now = new Date().getTime();
        const diff = now - new Date(Number(date)).getTime();
        const isAuth = diff < 1000 * 60 * 60 * 12;
        if (isAuth) {
          localStorage.setItem("token", aes.encrypt(name + "|" + now));
        }
        return isAuth;
      }
    }
  }
  return false;
}
